* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif, "Arial", sans-serif;
}


.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.container {
  max-width: @desctope-width;
  margin: 0 auto;
}

.body {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  padding: 0;
  background-color: @standart-white;
  position: relative;

  &--index {
    display: flex;
    flex-direction: column;
  }

  &--menu-open {
    overflow: hidden;

    &::before {
      content: '';
      z-index: 2;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(255, 255, 255, .6);
      cursor: pointer;
    }
  }

  &--modal-open {
    overflow: hidden;

    &::before {
      content: '';
      z-index: 3;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(255, 255, 255, .8);
      cursor: pointer;
    }
  }
}
